import React from "react";
import "../../stylesheets/FeaturedArticle.css";

const FeaturedArticle = ({ title, ctgType, datePublished, imgName, url }) => {
  const imgURL = require(`../../images/${imgName}`);
  const sectionStyle = {
    backgroundImage: `linear-gradient(to bottom, rgba(0, 0, 0, 0.0), rgba(0, 0, 0, 0.72)), url(${imgURL})`,
  };

  return (
    <div className="featured-article">
      <a href={url} target="_blank">
        <div className="featured-article__image" style={sectionStyle}></div>
        <div className="featured-article__info">
          <h3 className="featured-article__title">{title}</h3>
          <div className="featured-article__tags">
            <div className="featured-article__category">
              <span class="material-icons">local_offer</span>
              <h4>{ctgType}</h4>
            </div>
            <h4>•</h4>
            <h4 className="featured-article__date"> {datePublished}</h4>
          </div>
        </div>
      </a>
    </div>
  );
};

export default FeaturedArticle;
