import React, { useContext } from "react";
import SubArticle from "./SubArticle";
import "../../stylesheets/SubArticle.css";
import { SubArticleContext } from "./SubArticleContext";

const SubArticleList = () => {
  const [articles] = useContext(SubArticleContext);

  return (
    <div className="sub-articles">
      {articles.map((article) => (
        <SubArticle
          title={article.title}
          ctgType={article.ctgType}
          datePublished={article.datePublished}
          imgName={article.imgName}
          url={article.url}
        />
      ))}
    </div>
  );
};

export default SubArticleList;
