import React, { useContext } from "react";
import FeaturedArticle from "./FeaturedArticle";
import { FeaturedArticleContext } from "./FeaturedArticleContext";

const FeaturedArticleList = () => {
  const [articles] = useContext(FeaturedArticleContext);

  return (
    <div className="featured-articles-container">
      {articles.map((article) => (
        <FeaturedArticle
          title={article.title}
          ctgType={article.ctgType}
          datePublished={article.datePublished}
          imgName={article.imgName}
          url={article.url}
        />
      ))}
    </div>
  );
};

export default FeaturedArticleList;
